import { Card, CardMedia, CardContent, CardActions, Typography } from "@material-ui/core";
import { withStyles } from "@material-ui/core/styles";
import Link from "next/link";

const styles = {
  card: {
    maxWidth: 1000,
    display: "flex",
    marginBottom: 30,
    paddingLeft: 20,
    paddingTop: 10,
    paddingBottom: 10,
    maxHeight: 140
  },
  media: {
    width: "100%",
    maxWidth: 140
  },
  content: {
    paddingTop: 0,
  },
  hover:{
    "&:hover": {
      color: "blue",
      opacity: 0.6,
      cursor: "pointer"
    }
  },
  summary: {
    lineClamp: 3,
    boxOrient: "vertical",
    display: "box",
    overflow: "hidden"
  },
  meta: {
    display: 'block',
    marginTop: 5
  }
};

export default withStyles(styles)(props => {
  const {
    classes,
    show: {
      image: { medium },
      summary,
      name,
      id,
      premiered
    }
  } = props;

  const backendurl = process.env.BACKEND_URL;

  return (
    <Link href={`${backendurl}/post/${id}`}>
      <Card className={classes.card}>
        <CardMedia
          className={classes.media}
          image={medium || "/static/GitHub-Mark-64px.png"}
        />
        <CardContent className={classes.content}>
          <Typography gutterBottom variant="title" component="h2" className={classes.content}>
            {name}
          </Typography>
          <Typography component="p" className={[classes.content,classes.summary]}>
            {summary.replace(/<[/]?[pb]>/g, "")}
          </Typography>
          <Typography component="p" className={classes.meta}>
            {premiered}
          </Typography>
        </CardContent>
      </Card>
    </Link>
  );
});
